class Characters extends Component {
  constructor(props) {
    super(props);
  }

  render () {

    let current = this.props.currentTab;

    const characterDetails = this.props.data.map(function(character) {
        {if (character.id === current) {
            return (
              <div key={character.id} className='m_tabpanel' id='panel-4' aria-labelledby='tab-4' aria-hidden='true' role='tabpanel'>
                {characterIcon(character.id)}
                <div className='m_tabpanel_text' key={character.id}>
                  <input type='text' value={character.name} />
                  <p>{character.description}</p>
                </div>
              </div>
            );
          }
        };
    });

    function characterIcon(id) {
      switch(id) {
        case 1:
          return <IconCharlie />
        case 2:
          return <IconFranklin />
        case 3:
          return <IconLucy />
        case 4:
          return <IconSnoopy />
      }
    };

    return (
      <span>
        {characterDetails}
      </span>
    )
  }
}

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentTab: 1,
      width: props.width,
      goMobile: false,
      tabText: '',
      characters: []
    };
  };

  componentDidMount(x,y,z) {
    window.addEventListener("resize", this.updateDimensions);
    this.setState({width:window.innerWidth});

    var _this = this;
    this.serverRequest =
      axios
        .get("http://localhost:3000/src/data/data.json")
        .then(function(result) {
          _this.setState({
            characters: result.data
          });
        })
        .catch(function (error) {
          console.log(error);
        });
  };

  componentWillUnmount = () => {
    this.serverRequest.abort();
  };

  updateDimensions = () => {
    this.setState({width:window.innerWidth});
    this.updateUI();
  };

  /*
  TODO: figure out how to retain current selected tab when resizing
  */
  updateUI = () => {
    if(this.state.width < 500) {
      this.setState({currentTab: 5, goMobile: true})
    } else {
      this.setState({currentTab: 1, goMobile: false})
    }
  };

  changeTab = (tab) => {
    this.setState({currentTab: tab.id});
  };

  render () {

    /*
     TODO: learn how to use this instead of getting width through JS
    */
    const mediaQuery = window.matchMedia('(min-width: 500px)');

    const items = this.state.characters;

    return (
      <div className="c_tabsSwitcher">
        <Tabs
          currentTab={this.state.currentTab}
          changeTab={this.changeTab}
          data={items}
          className={this.state.goMobile ? 'nav--is-hidden' : ''}
        />
        <div className="c_tabpanels">
          {!this.state.goMobile ? (
            <Characters data={items} currentTab={this.state.currentTab} />
          ) : (
            <span>
               <Charlie />
               <Franklin />
               <Lucy />
               <Snoopy />
            </span>
          )}
        </div>
      </div>
    );
  }
}

export default Home



